.hero-section {
  max-width: 100%;
  margin: 0;
  padding: 0;
  position: relative;
  min-height: 550px;
  display: flex;
  align-items: center;
  overflow: hidden;

  background-color: transparent !important;
  box-shadow: none !important;
  border-radius: 0 !important;
}
.hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 50, 100, 0.8);
  z-index: 1;
  display: block;
}
.hero-content-wrapper {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 40px 20px;
  display: flex;
  align-items: center;
  gap: 40px;
  position: relative;
  z-index: 2;
}
.hero-content {
  flex: 1;
  max-width: 50%;
  text-align: left;
  position: relative;
  z-index: 3;
  background-color: transparent;
  padding: 0;
}
.hero-section h1 {
  font-size: 48px;
  color: #ffd700;
  margin-bottom: 20px;
  line-height: 1.1;
  font-weight: 800;
  text-transform: uppercase;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}
.hero-subtitle {
  font-size: 20px;
  color: #fff;
  margin-bottom: 50px;
  line-height: 1.6;
  font-weight: 400;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
}

.hero-actions {
  display: flex;
  gap: 20px;
  justify-content: flex-start;
}

.cta-primary {
  text-decoration: none;
  background-color: #ffd700;
  color: #003366;
  padding: 14px 30px;
  border-radius: 8px;
  font-weight: 700;
  font-size: 16px;
  border: 2px solid #ffd700;
  letter-spacing: 0.5px;
  transition: all 0.3s ease;
}

.cta-primary:hover {
  background-color: #e6c200;
  color: #000;
}
.cta-secondary-white {
  text-decoration: none;
  background-color: transparent;
  color: #fff;
  border: 2px solid #fff;
  padding: 14px 30px;
  border-radius: 8px;
  font-weight: 700;
  font-size: 16px;
  letter-spacing: 0.5px;
  transition: all 0.3s ease;
}

.cta-secondary-white:hover {
  background-color: #fff;
  color: #003366;
}

.hero-image-container {
  flex: 1;
  max-width: 50%;
  height: 100%;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  min-height: 350px;
  overflow: hidden;
  position: relative;
  z-index: 2;
}
@media (max-width: 900px) {
  .hero-content-wrapper {
    flex-direction: column;
    text-align: center;
    padding: 40px 15px;
    gap: 20px;
  }

  .hero-section {
    min-height: 600px;
  }

  .hero-content,
  .hero-image-container {
    max-width: 100%;
  }

  .hero-section h1 {
    font-size: 38px;
  }

  .hero-actions {
    flex-direction: column;
    gap: 15px;
    justify-content: center;
  }

  .cta-primary,
  .cta-secondary-white {
    width: 80%;
    margin: 0 auto;
  }
}
