.service-detail-page {
  padding: 30px 0;
  background-color: #f8f8f8;
}

.container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

.service-detail-grid {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 40px;
}

@media (max-width: 992px) {
  .service-detail-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
}

.detail-content-column {
  background-color: #ffffff;
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
}

.detail-title {
  color: #004d99;
  font-size: 2.2rem;
  font-weight: 700;
  margin-bottom: 20px;
  line-height: 1.3;
}

.detail-hero-image {
  width: 100%;
  height: auto;
  max-height: 350px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 25px;
}

.detail-summary {
  font-size: 1.1rem;
  line-height: 1.6;
  color: #333;
  font-weight: 500;
  margin-bottom: 30px;
  padding: 15px;
  border-left: 5px solid #ff9900;
  background-color: #fff9f0;
}

.detail-section {
  margin-bottom: 35px;
}

.section-heading {
  color: #004d99;
  font-size: 1.7rem;
  font-weight: 600;
  margin-bottom: 15px;
  padding-bottom: 5px;
  border-bottom: 2px dashed #e0e0e0;
}

.section-list {
  list-style: none;
  padding-left: 0;
}

.section-list li {
  font-size: 1rem;
  line-height: 1.8;
  color: #444;
  padding-left: 25px;
  position: relative;
  margin-bottom: 8px;
}
.section-list li::before {
  content: '✅';
  position: absolute;
  left: 0;
  color: #ff9900;
  font-weight: bold;
}

.detail-cta {
  background-color: #f0f8ff;
  padding: 30px;
  border-radius: 8px;
  text-align: center;
  border: 1px solid #cceeff;
  margin-top: 40px;
}

.detail-cta p {
  font-size: 1.1rem;
  color: #004d99;
  margin-bottom: 20px;
  font-weight: 600;
}

.cta-primary-orange {
  display: inline-block;
  background-color: #ff6600;
  color: #ffffff;
  font-size: 1.2rem;
  font-weight: 700;
  padding: 15px 30px;
  border-radius: 5px;
  text-decoration: none;
  transition:
    background-color 0.3s,
    transform 0.2s;
  letter-spacing: 1px;
  box-shadow: 0 4px 6px rgba(255, 102, 0, 0.4);
}

.cta-primary-orange:hover {
  background-color: #e65c00;
  transform: translateY(-2px);
}

.detail-sidebar {
  position: sticky;
  top: 30px;
  height: fit-content;
}

.sidebar-block {
  background-color: #ffffff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
  margin-bottom: 20px;
}

.sidebar-title {
  font-size: 1.4rem;
  color: #004d99;
  border-bottom: 2px solid #ff9900;
  padding-bottom: 10px;
  margin-bottom: 15px;
  font-weight: 700;
}
.contact-promo {
  background-color: #f0f8ff;
  text-align: center;
  border: 1px solid #004d99;
}

.contact-promo .sidebar-title {
  border-bottom: none;
  color: #ff6600;
}

.contact-promo p {
  color: #333;
  font-size: 1rem;
  margin-bottom: 10px;
}

.sidebar-hotline {
  display: block;
  font-size: 1.6rem;
  font-weight: 800;
  color: #ff6600;
  text-decoration: none;
  margin: 10px 0 15px 0;
  transition: color 0.2s;
}

.sidebar-hotline:hover {
  color: #e65c00;
}
.category-list {
  list-style: none;
  padding: 0;
}

.category-list li {
  margin-bottom: 10px;
  border-bottom: 1px dashed #eee;
  padding-bottom: 5px;
}

.category-list li a {
  color: #444;
  text-decoration: none;
  font-size: 1rem;
  transition: color 0.2s;
  display: block;
  padding: 5px 0;
}

.category-list li a:hover {
  color: #ff9900;
  padding-left: 5px;
}

.category-list li.active-category a {
  color: #004d99;
  font-weight: 700;
  background-color: #f0f8ff;
  padding-left: 8px;
  border-radius: 4px;
}
